import { configureStore } from "@reduxjs/toolkit";
import { createLogger } from "redux-logger";
import { persistStore, persistReducer } from "redux-persist";
import storage from "redux-persist/lib/storage"; // Defaults to localStorage for web
import { combineReducers } from "redux";
import menuReducer from "./menuSlice";
import onlineStoreReducer from "./onlineStoreSlice";

// Logger middleware configuration
const logger = createLogger({
    collapsed: true, // Collapse action logs for readability
    diff: true, // Show state diff
});

// Persist configuration
const persistConfig = {
    key: "root",
    storage,
    whitelist: ["onlineStore"], // Persist only the onlineStore reducer
};

// Combine reducers
const rootReducer = combineReducers({
    restaurant: menuReducer,
    onlineStore: onlineStoreReducer,
});

// Create a persisted reducer
const persistedReducer = persistReducer(persistConfig, rootReducer);

// Configure the store
const store = configureStore({
    reducer: persistedReducer,
    middleware: (getDefaultMiddleware) =>
        getDefaultMiddleware({
            serializableCheck: false, // Required for redux-persist
        }).concat(logger), // Add logger middleware
});

// Create the persistor
export const persistor = persistStore(store);

/*
UNCOMMENT TO PURGE REDUX
*/
// persistor.purge();

export default store;
